.module-cta-grid {
	padding: 20px;

	.ctas {
		display: flex;
		flex-wrap: wrap;
	}

	.cta {
		position: relative;
		display: block;
		text-decoration: none;
		margin-bottom: 20px;
		overflow: hidden;

		&:after {
			transition: all 0.25s;
		}

		&:hover,
		&:active,
		&:focus {
			
			&:after {
				background-color: rgba($primary, 1);
				opacity: 0.6;
			}
		}

		.cta-image {
			height: 100%;
			left: 0;
			position: absolute;
			width: 100%;
			top: 0;
		}

		.cta-content {
			width: 100%;
			padding: 20px;
			padding-top: 30%;
		}

			.cta-title {
				font-size: 26px;
				font-weight: 700;
				margin-bottom: 20px;
				color: #fff;
			}

			.cta-description {
				
				p {
					color: #fff;
				}
			}
	}

	.flex-col-2 {

		@include media-breakpoint-up(lg) {
			margin-bottom: -20px;
		}

		.flex-col-item {
			@include media-breakpoint-up(md) {
				width: calc(50% - 10px);
				margin-right: 20px;

				&:nth-child(2n) {
					margin-right: 0;
				}
			}
		}
	}

	.flex-col-3 {

		@include media-breakpoint-up(lg) {
			margin-bottom: -20px;
		}

		.flex-col-item {

			@include media-breakpoint-up(lg) {
				width: calc(33.333333% - 14px);
				margin-right: 20px;

				&:nth-child(3n) {
					margin-right: 0;
				}
			}
		}
	}

	.flex-col-4 {

		.flex-col-item {
			@include media-breakpoint-up(xl) {
				width: calc(25% - 15px);
				margin-right: 20px;

				&:nth-child(2n) {
					margin-right: 20px;
				}

				&:nth-child(4n) {
					margin-right: 0;
				}
			}
		}
	}
}
