.module-50-50 {
	
	.flex {
		display: flex;
		flex-direction: column-reverse;
		
		@include media-breakpoint-up(lg) {
			flex-direction: row;
		}
	}

	.module-part-content {
		padding: 50px 35px;
		width: 100%;

		@include media-breakpoint-up(lg) {
			padding: 90px;
			width: 50%;
		}
	}

		.module-part-content-wrap {
			max-width: 570px;
			margin-left: auto;
			margin-right: auto;
		}

			.module-part-heading {}

				.module-part-title {}

			.module-part-buttons {
				margin-bottom: -10px;

				a {
					margin-right: 10px;
					margin-bottom: 10px;

					&:last-child {
						margin-right: 0;
					}
				}
			}

	.module-part-image {
		position: relative;
		padding: 20px;
		padding-bottom: 0;
		width: 100%;

		@include media-breakpoint-up(lg) {
			width: 50%;
			padding: 0;
		}
		
		img {
			display: block;
			width: 100%;
			max-height: 400px;

			@include media-breakpoint-up(lg) {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				width: 100%;
				height: 100%;
				max-height: none;
			}
		}

		.btn-play {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
		}

		iframe {
			display: block;
			width: 100%;
		}
	}

	&.module-setting-image-side-left {
		
		@include media-breakpoint-up(lg) {
			
			.flex {
				display: flex;
				flex-direction: row-reverse;
			}
		}
	}

	&.module-setting-bg-dark {
		* {
			color: #fff;
		}
	}

	&.module-setting-width-full {

		.module-part-image {

			@include media-breakpoint-up(lg) {
				iframe {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					height: 100%;
				}
			}
		}
	}
	
	&.module-setting-width-contained {

		.flex {
			align-items: flex-start;

			@include media-breakpoint-up(xxl) {
				max-width: 1240px !important;
				margin-left: auto;
				margin-right: auto;
			}
		}

		.module-part-content {

			@include media-breakpoint-up(lg) {
				padding: 0;
				padding-right: 90px;
			}
		}
		
		.module-part-image {

			img {

				@include media-breakpoint-up(lg) {
					position: relative;
					width: 100%;
					height: auto;
				}
			}
		}
	}

	&.module-setting-width-contained.module-setting-image-side-left {
		
		.module-part-content {

			@include media-breakpoint-up(lg) {
				padding-right: 0;
				padding-left: 90px;
			}
		}
	}

}