.module-testimonial {
	padding: 50px 0;

	@include media-breakpoint-up(lg) {
		padding: 120px 0;
	}


	.module-part-content {
		max-width: 1000px;
		margin-left: auto;
		margin-right: auto;

		blockquote {
			margin: 0;
			padding: 0;
		}

		p {
			font-size: 26px;
			font-weight: 500;
			font-family: $header-font;
			margin-bottom: 30px;
			font-style: italic;
			line-height: 1.538461538;
		}

		cite {
			font-size: 26px;
			font-weight: 700;
			font-style: normal;
			font-family: $header-font;

			span {
				display: block;
				font-size: 12px;
				text-transform: uppercase;
				margin-top: 5px;
				letter-spacing: 2px;
			}
		}
	}

	&.module-setting-bg-dark {

		cite {
			color: #fff;
		}
	}
}