$logo-width-mobile: 180px;
$logo-width-desktop: 240px;
$logo-width-desktop-half: $logo-width-desktop / 2;

body {
	background: #fff;
	
	&:before {
		display: none;
		content: "mobile";

		@media(min-width: $nav-breakpoint) {
			content: "desktop";
		}
	}

	&.site-nav-is-open {
		@media(max-width: $nav-breakpoint - 1) {
			overflow: hidden;
		}
	}

	&:not(.transparent-header) {
		// padding-top: 96px;
	}
}

.site-header {
	position: sticky;
	z-index: $z-index-header;
	top: 0;
	left: 0;
	right: 0;
	transition: box-shadow 0.25s;
	box-shadow: $shadow-3;
}

	.site-header-wrap {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding: 15px;
		background-color: #fff;
		transition: background-color 0.25s;

		@media(min-width: $nav-breakpoint) {
			padding: 25px 50px;
		}
	}

	.site-branding {
		margin-right: auto;
		order: 1;
		width: $logo-width-mobile;
		transition: width 0.25s;

		@media(min-width: $nav-breakpoint) {
			width: $logo-width-desktop;
		}

		a {
			display: block;

			img {
				display: block;
				width: 100%;
				height: auto;
				transition: all 0.25s;
			}
		}
	}

	.site-nav-actions {
		order: 5;
		display: flex;
		align-items: center;
		margin-right: 30px;

		> div {
			margin-left: 20px;

			&:first-child {
				margin-left: 0;
			}

			@media(min-width: $nav-breakpoint) {
				margin-left: 15px;
			}
		}

		@media(min-width: $nav-breakpoint) {
			order: 15;
			margin-left: 30px;
			margin-right: 0;
		}

		path {
			fill: $black-60;
			transition: fill 0.25s;
		}

		a, button {
			display: block;

			svg {
				display: block;
			}

			&:hover,
			&:active,
			&:focus {
				
				path {
					fill: $black;
				}
			}
		}
	}

		.site-nav-cart {

			a {
				position: relative;
			}
		}

			.site-nav-cart-total {
				position: absolute;
				width: 21px;
				height: 21px;
				top: -10px;
				right: -15px;
				border-radius: 100%;
				color: #fff;
				background: $primary;
				text-align: center;
				line-height: 21px;
				vertical-align: middle;
				font-family: $header-font;
				font-weight: bold;
				font-size: 11px;
			}

	.site-nav {
		display: block;
		height: auto;
		margin: 0;
		padding: 0;
		order: 10;

		a {
			text-decoration: none;
		}
	}

		.site-nav-toggle {
			position: relative;
			z-index: +10;
			border: none;
			background-color: transparent;
			width: 30px;
			padding: 10px;
			height: 30px;
			border-radius: 100%;
			display: block;

			@media(min-width: $nav-breakpoint) {
				display: none;
			}
		}

			.site-nav-toggle-bar {
				display: block;
				width: 100%;
				height: 2px;
				position: absolute;
				background-color: $black;
				transition: all 0.25s;
				left: 0;
			}

				.site-nav-toggle-bar-top {
					top: 5px;

					.site-nav-is-open & {
						top: 13px;
						transform: rotate(45deg);
					}
				}

				.site-nav-toggle-bar-middle {
					top: 50%;
					transform: translateY(-50%);
					left: 0;
					right: 0;
					margin-left: auto;
					margin-right: auto;

					.site-nav-is-open & {
						width: 0;
					}
				}

				.site-nav-toggle-bar-bottom {
					bottom: 5px;

					.site-nav-is-open & {
						bottom: auto;
						top: 13px;
						transform: rotate(-45deg);
					}
				}
	
	.site-nav-wrap {

		@media(max-width: $nav-breakpoint - 1) {
			background-color: #fff;
			overflow: scroll;
			-webkit-overflow-scrolling: touch;
			padding: 30px;
		}

	}

	.site-nav-menu {
		margin: 0;
		padding: 0;
		list-style-type: none;

		@media(min-width: $nav-breakpoint) {
			display: flex;
			align-items: stretch;
			list-style-type: none;
		}

		> li {
			margin: 0;
			padding: 0 0 10px;
			border-bottom: 1px solid rgba($black, 0.1);

			&:last-child {
				border-bottom: none;
			}

			&:hover,
			&:active,
			&:focus {
				
				> a {
					color: $primary;
				}
			}

			@media(min-width: $nav-breakpoint) {
				display: flex;
				align-items: center;
				position: relative;
				z-index: +5;
				margin: 0;
				padding: 0;
				border-bottom: none;
			}

			> a {
				display: block;
				font-size: 16px;
				font-weight: 700;
				text-transform: uppercase;
				letter-spacing: 4.92px;
				padding: 20px 0 10px;
				color: $black;
				font-family: $header-font;
				transition: all 0.25s;

				@media(min-width: $nav-breakpoint) {
					letter-spacing: 1.3px;
					font-size: 14px;
					padding: 10px 30px;

					&:hover,
					&:active,
					&:focus {
						color: $primary;
					}

					svg {
						display: none;
					}
				}

				&.btn-primary-outline,
				&.btn-primary {
					padding: 10px 20px;
					margin-top: 30px;

					&:hover,
					&:active,
					&:focus {
						color: #fff !important;
					}

					@media(min-width: $nav-breakpoint) {
						margin-top: 0;
						// padding: 6px 20px;
						min-width: 0;
					}
				}

				&.btn-primary {
					color: #fff !important;
				}

				&.site-nav-menu-dropdown {
					display: flex;
					justify-content: space-between;
					align-items: center;

					svg {
						transition: transform 0.25s;
						opacity: 0.25;

						path {
							fill: $black;
						}
					}

					&[aria-expanded="true"] {
						svg {
							transform: rotate(180deg);
						}
					}
				}
			}
			
			// second level menu items
			> ul {
				display: none;
				list-style: none;
				margin: 0;
				padding: 0;

				@media(min-width: $nav-breakpoint) {
					display: block;
					position: absolute;
					z-index: 999999;
					top: 100%;
					transform: translateY(50px) translateX(-50%);
					left: 50%;
					width: 220px;
					background-color: #fff;
					margin: 0;
					padding: 20px 30px;
					list-style-type: none;
					border-radius: 4px;
					box-shadow: $shadow-3;
					transition: transform 0.25s, opacity 0.25s;
					opacity: 0;
					pointer-events: none;

					&.is-open {
						transform: translateY(0px) translateX(-50%);
						opacity: 1;
						pointer-events: auto;
					}

					&:before {
						position: absolute;
						content: "▲";
						width: 30px;
						text-align: center;
						height: 10px;
						transform: scaleX(2);
						text-shadow: 0 -5px 10px rgba($black, 0.1);
						color: #fff;
						top: -16px;
						left: 0;
						right: 0;
						margin-left: auto;
						margin-right: auto;
					}
				}

				> li {
					margin: 0;
					padding: 0;

					> a {
						display: block;
						font-size: 16px;
						letter-spacing: 0;
						padding: 10px 15px;
						font-weight: 500;
						color: $black-60;
						transition: color 0.25s;
						font-family: $header-font;

						&:hover,
						&:active,
						&:focus {
							color: $black;
						}

						&:first-child {
							padding-top: 0;
						}

						@media(min-width: $nav-breakpoint) {
							display: block;
							color: $black-60;
							font-size: 13px;
							font-weight: 500;
							text-transform: uppercase;
							letter-spacing: 1px;
							padding: 10px 0;

							&:first-child {
								padding-top: 10px;
							}

							&:hover,
							&:active,
							&:focus {
								color: $black;
							}
						}
					}
				}
			}
		}
	}

.transparent-header {

	.site-header {
		position: fixed;
	}

	.site-branding {

		a {
			position: relative;
		}
	}

		.site-branding-logo {
			opacity: 0;
		}

		.site-branding-logo-scrolled {
			position: absolute;
			top: 0;
			left: 0;
			opacity: 1;
		}
}

.transparent-header:not(.is-scrolled) {

	.site-header {
		box-shadow: none;
	}

		.site-branding-logo {
			opacity: 1;
		}

		.site-branding-logo-scrolled {
			opacity: 0;
		}

		.site-header-wrap {
			background-color: transparent;
		}

		.site-nav-toggle-bar {
			background-color: #fff;
		}

	.site-nav-menu > li > a {

		@media(min-width: $nav-breakpoint) {
			color: #fff;

			&:hover,
			&:active,
			&:focus {
				color: $primary;
			}
		}
	}

	.site-nav-actions path {
		fill: $white-60;
	}

	.site-nav-actions a {

		&:hover,
		&:active,
		&:focus {
			
			path {
				fill: #fff;
			}
		}
	}
}

.transparent-header-enabled.solid-header {

	.site-branding-logo {
		display: none;
	}
}

.nav-overlay {

	.site-nav-wrap {

		@media(max-width: $nav-breakpoint - 1) {
			position: fixed;
			z-index: +5;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			opacity: 0;
			pointer-events: none;
			transition: opacity 0.25s;
			padding: 60px;
		}
	}

	&.site-nav-is-open {

		.site-nav-wrap {
			opacity: 1;
			pointer-events: auto;
		}
	}
}

.nav-offcanvas {

	.site-header,
	.site-content,
	.site-footer,
	.site-attribution {
		transition: transform 0.3s;

		.site-nav-is-open & {
			@media(max-width: $nav-breakpoint - 1) {
				transform: translateX(-320px);
			}

			@media(max-width: 385px) {
				transform: translateX(calc(-100vw + 65px));
			}
		}
	}

	.site-header {
		transition: all 0.3s;
	}

		.site-nav-wrap {

			@media(max-width: $nav-breakpoint - 1) {
				position: fixed;
				top: 0;
				right: 0;
				bottom: 0;
				width: 320px;
				transform: translateX(320px);
				height: 100vh;
				transition: transform 0.3s;
				padding-top: 10px;
			}

			@media(max-width: 385px) {
				width: calc(100vw - 65px);
				transform: translateX(calc(100vw - 65px));
			}

		}

	&.site-nav-is-open {

		.site-header,
		.site-content,
		.site-footer,
		.site-attribution {
			@media(max-width: $nav-breakpoint - 1) {
				transform: translateX(-320px);
			}

			@media(max-width: 385px) {
				transform: translateX(calc(-100vw + 65px));
			}
		}
	}
}

.site-header-logo-center {

	.site-branding {

		@media(min-width: $nav-breakpoint) {
			position: absolute;
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
			top: 50%;
			transform: translateY(-50%);
		}
	}

	.site-nav {
		@media(min-width: $nav-breakpoint) {
			width: 100%;
		}
	}

		.site-nav-wrap {
			@media(min-width: $nav-breakpoint) {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
		}

			.site-nav-menu {

				@media(max-width: $nav-breakpoint - 1) {

					&:first-child {
						border-bottom: 1px solid rgba($black, 0.1);
					}
				}

				@media(min-width: $nav-breakpoint) {
					width: calc(50% - #{$logo-width-desktop-half});

					&:last-child {
						justify-content: flex-end;

						.nav-ecommerce & {
							padding-right: 70px;
						}
					}

					> li {

						> a {
							padding-left: 15px;
							padding-right: 15px;
						}
					}
				}

				@media(min-width: 1400px) {
					
					> li {

						> a {
							padding-left: 25px;
							padding-right: 25px;
						}
					}
				}
			}

	.site-nav-actions {
		@media(min-width: $nav-breakpoint) {
			position: absolute;
			right: 50px;
			top: 50%;
			transform: translateY(-50%);
		}
	}
}

.nav-dark {

	@media(max-width: $nav-breakpoint - 1) {

		&.site-nav-is-open.nav-overlay {

			.site-nav-toggle-bar {
				background-color: #fff;
			}
		}

		.site-header-logo-center {

			.site-nav-menu {
				&:first-child {
					border-bottom: 1px solid rgba(#fff, 0.25);
				}
			}
		}

		.site-nav-wrap {
			background-color: $black;
		}

		.site-nav-menu {

			> li {
				border-color: rgba(#fff, 0.25);

				> a {
					color: #fff;

					&:hover,
					&:active,
					&:focus {
						color: $primary;
					}

					&.site-nav-menu-dropdown svg path {
						fill: #fff;
					}
				}

				> ul {

					> li {

						> a {
							color: $white-60;

							&:hover,
							&:active,
							&:focus {
								color: $white;
							}
						}
					}
				}
			}
			
		}
	}
}

.transparent-header-enabled {

	&.single-product,
	&.single-post {

		.site-branding-logo {
			display: none !important;
		}
	}
}