.page-banner {
	padding: 60px 40px;

	@include media-breakpoint-up(lg) {
		padding: 90px 0;
	}
}

	.page-banner-image {}

	.page-banner-content {
		text-align: center;

		.page-banner-subtitle,
		.term-description p {
			margin-top: 20px;
			color: #fff;
		}
	}

		.page-banner-title {
			margin-bottom: 0;
			font-size: 38px;
			line-height: 1.152777778;
			color: #fff;

			@include media-breakpoint-up(lg) {
				font-size: 72px;
			}
		}

		.page-banner-buttons {
			max-width: 400px;
			margin-left: auto;
			margin-right: auto;
			margin-top: 30px;
			margin-bottom: -16px;

			@include media-breakpoint-up(md) {
				max-width: none;
				margin-left: 0;
				margin-right: 0;
			}

			.btn-primary-outline {
				color: #fff;
			}

			a {
				display: block;
				width: 100%;
				margin-bottom: 16px;

				@include media-breakpoint-up(md) {
					display: inline-block;
					width: auto;
					margin-right: 16px;

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

	.page-banner-scroll {
		position: absolute;
		z-index: +40;
		bottom: 40px;
		left: 0;
		right: 0;
		width: 100px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;

		&:hover,
		&:active,
		&:focus {
			
			svg {
				transform: translateY(10px);
			}
		}

		span {
			display: inline-block;
			text-transform: uppercase;
			font-family: $header-font;
			font-size: 14px;
			line-height: 1;
			margin-bottom: 10px;
			color: #fff;
			transition: color 0.25s;
		}

		svg {
			display: inline-block;
			transition: transform 0.25s;
		}
	}

.home .page-banner {
	padding-bottom: 160px;

	@include media-breakpoint-up(lg) {
		padding: 120px 0 220px;
	}

	.page-banner-content {
		max-width: 720px;
		margin-left: auto;
		margin-right: auto;
	}

	&.page-banner-text-align-left {

		.page-banner-content {
			text-align: left;
			margin: 0;
		}
	}
}

.transparent-header .page-banner {
	padding-top: 150px;

	@include media-breakpoint-up(lg) {
		padding-top: 240px;
	}
}

.page-banner-image-off {
	padding: 40px 0;

	&:after {
		display: none;
	}

	@include media-breakpoint-up(lg) {
		padding: 60px 0;
	}

	.page-banner-title,
	.page-banner-content .page-banner-subtitle,
	.page-banner-content .term-description p {
		color: $black;
	}
}

.page-banner-image-off + .woocommerce-breadcrumb,
.page-banner-image-off + .module-padded-top.module-setting-bg-white {
	padding-top: 0 !important;
}