table:not(.gfield_list):not(.ui-datepicker-calendar) {
	width: 100%;
	border-collapse: collapse;
	border-radius: 3px;
	overflow: hidden;

	thead {

		th {
			text-align: left;
			text-transform: uppercase;
			font-size: 14px;
			letter-spacing: 1.5px;
			font-family: $header-font;
		}
	}

	tfoot {

		th {
			font-family: $header-font;
		}
	}

	th,
	td {
		line-height: 1.3;
		padding: 10px;

	}
}