.module-full-width-cta {
	padding: 60px 0;

	@include media-breakpoint-up(lg) {
		padding: 120px 0;
	}

	.content-wrap {
		max-width: 690px;
		margin-left: auto;
		margin-right: auto;

		*:not([class|="btn"]) {
			color: $white;
		}
	}

	.module-part-content {
		text-align: center;
	}

		.module-part-content-wrap {
			max-width: 620px;
			margin-left: auto;
			margin-right: auto;

			p {
				color: #fff;

				&:last-child {
					margin-bottom: 40px;
				}
			}

			.gform_confirmation_message {
				color: #fff;
			}
		}

		.module-part-title {
			max-width: 720px;
			margin-left: auto;
			margin-right: auto;
			color: #fff;
			margin-bottom: 15px;
		}
}
