.module-team {
	padding: 50px 0;

	@include media-breakpoint-up(lg) {
		padding: 80px 0 40px;
	}

	.module-part-heading {
		text-align: center;
	}

		.module-part-title {
			max-width: 940px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 0;
		}

	.module-part-heading + .module-part-team {
		margin-top: 20px;

		@include media-breakpoint-up(lg) {
			margin-top: 40px;
		}
	}

	.module-part-team {

		@include media-breakpoint-down(sm) {
			max-width: 350px;
			margin-left: auto;
			margin-right: auto;
		}

		.team-card {
			box-shadow: $shadow-3;
			background-color: #fff;
			display: flex;
			flex-direction: column;
		}

		button.team-card {
			&:hover,
			&:active,
			&:focus {
				
				h3 {
					color: $primary;
				}
			}
		}

			.team-card-image {
				display: block;
				width: 100%;
				height: 250px;
			}

			.team-card-content {
				padding: 20px 30px;
				width: 100%;

				h3 {
					margin-bottom: 0;
					transition: color 0.25s;
				}

				p {
					line-height: 1.4;
					margin-top: 10px;
				}
			}
	}
}