/*
 * Shared checkbox and radio styles
 */
.gfield_checkbox,
.gfield_radio {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
        font-family: $header-font;

        &:not(:first-child) {
            margin-top: 10px;
        }
    }

    input {
        opacity: 0;
        position: absolute;
    }

    label {
        cursor: pointer;
        display: inline-block;
        vertical-align: middle;

        &::before {
            border: 1px solid $black-25;
            content: '';
            cursor: pointer;
            display: inline-block;
            height: 20px;
            margin-right: 10px;
            vertical-align: middle;
            width: 20px;
        }
    }
}

/*
 * Checkbox styles
 */
.gfield_checkbox {
    input:checked + label {
        font-weight: 700;

        &::before {
            background: url('../img/forms/check-mark.svg') center center no-repeat, $accent-1;
            border: 1px solid $accent-1;
        }
    }

    label::before {
        border-radius: 3px;
    }
}

/*
 * Radio styles
 */
.gfield_radio {
    input:checked + label {
        font-weight: 700;

        &::before {
            background: $accent-1;
            background-clip: content-box;
            padding: 3px;
        }
    }

    label::before {
        border-radius: 50%;
    }
}

.gchoice {
    position: relative;
}