.module-mini-slider {
	padding: 50px 0;
	overflow: hidden;

	@include media-breakpoint-up(lg) {
		padding: 60px 0;
	}

	.slick-dotted.slick-slider {
		margin: 0;
	}

	.slick-list {
		overflow: visible;
	}

	.slides {

		.slick-track {
			display: flex;
		}

			

		.slick-dots {
			bottom: initial;
			margin-top: 35px;
			position: relative;

			li {
				button::before {
					font-size: 18px;
					opacity: 1;
					color: $black-10;
				}

				&.slick-active {
					button::before {
						color: $accent-2;
					}
				}
			}
		}
	}

		.slide {
			display: flex;
			padding: 0 12px;
			height: auto;

			@include media-breakpoint-up(md) {
				padding: 0 20px;
			}

			&.slick-center {
				.slide-content {
					transform: scale(1);
					opacity: 1;
				}
			}
		}

			.slide-wrap {
				align-items: center;
				display: flex;
				position: relative;
				transition: 0.3s;
				width: 100%;
			}

			.slide-content {
				padding: 100px 15px 60px;
				transform: scaleY(0.9);
				opacity: 0.6;
				transition: all 0.25s;
				width: 100%;
				box-shadow: $shadow-3;

				@include media-breakpoint-up(md) {
					transform: scaleY(0.9);
					padding: 60px;
					padding-top: 200px;
				}

				* {
					color: #fff;
				}
			}

				.slide-text{
					max-width: 550px;
				}

					.slide-title {
						font-size: 38px;
						max-width: 400px;
					}

	&.module-setting-bg-dark {
		.slick-dots {
			
			li {
				button::before {
					color: rgba(#fff, 0.1);
				}

				&.slick-active {
					button::before {
						color: #fff;
					}
				}
			}
		}
	}
	
}
