%btn {
	border: 0;
	border-radius: 39px;
	color: $white;
	cursor: pointer;
	display: inline-block;
	font-family: $header-font;
	font-size: 14px;
	font-weight: 700;
	letter-spacing: 1px;
	min-width: 195px;
	padding: 20px 30px;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase !important;
	transition: background-color 0.25s, color 0.25s;
	line-height: 1.4;

	&:hover,
	&:active,
	&:focus {
		color: #fff;
	}
}

// style for resetting buttons
.btn-reset {
	background-color: transparent;
	border: none;
	appearance: none;
	padding: 0;
}

/*
 * Normal sized buttons
 */

.btn-primary {
	@extend %btn;
	background-color: $primary;

	&:hover,
	&:active,
	&:focus {
		background-color: $primary-dark;
	}
}

.btn-primary-outline {
	@extend %btn;
	border: 2px solid $primary;
	background-color: transparent;
	padding: 18px 30px;
	color: $black;

	&:hover,
	&:active,
	&:focus {
		background-color: $primary-dark;
		border-color: $primary-dark;
		color: #fff;
	}
}

.btn-accent-1 {
	@extend %btn;
	background-color: $accent-1;

	&:hover,
	&:active,
	&:focus {
		background-color: $accent-1-dark;
	}
}

.btn-accent-2 {
	@extend %btn;
	background-color: $accent-2;

	&:hover,
	&:active,
	&:focus {
		background-color: $accent-2-dark;
	}
}

.btn-accent-3 {
	@extend %btn;
	background-color: $accent-3;

	&:hover,
	&:active,
	&:focus {
		background-color: $accent-3-dark;
	}
}

.btn-accent-4 {
	@extend %btn;
	background-color: $accent-4;

	&:hover,
	&:active,
	&:focus {
		background-color: $accent-4-dark;
	}
}

.btn-white {
	@extend %btn;
	background-color: $white;
	color: $black;

	&:hover,
	&:active,
	&:focus {
		background-color: $white;
		color: $primary;
	}
}

.btn-white-outline {
	@extend %btn;
	border: 1px solid $white;
	color: $white;

	&:hover,
	&:active,
	&:focus {
		background-color: $white;
		color: $black;
	}
}

.btn-black-ghost {
	@extend %btn;
	border: 1px solid $black;
	color: $black;

	&:hover,
	&:active,
	&:focus {
		background-color: $black;
		color: $white;
	}
}

.btn-play {
	width: 111px;
	height: 111px;
	padding: 0;
	margin: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100%;
	background-color: #fff;
	transition: background-color 0.25s;

	span {
		position: absolute;
	}

	svg {
		display: inline-block;
		margin-left: 5px;

		path {
			fill: $primary;
			transition: fill 0.25s;
		}
	}

	&:hover,
	&:active,
	&:focus {
		background-color: $primary;

		path {
			fill: #fff;
		}
	}
}