/*
 * Progress Bar
 */
.gf_progressbar {
    border: 1px solid $black-25;
    border-radius: 40px;

    .gf_progressbar_percentage {
        align-items: center;
        border-radius: 40px;
        display: flex;
        height: 30px;
        justify-content: center;

        &.percentbar_blue {
            background-color: $accent-1;
        }

        &.percentbar_gray {
            background-color: $black-25;
        }

        &.percentbar_green {
            background-color: $accent-4;
        }

        &.percentbar_orange {
            background-color: $accent-2;
        }

        &.percentbar_red {
            background-color: $accent-2-dark;
        }

        span {
            font-family: $header-font;
        }
    }
}

/*
 * Steps
 */
.gf_page_steps {
    border-bottom: 2px solid $black-25;
    display: flex;
    justify-content: center;
    margin: 15px 0;
    padding-bottom: 15px;

    @include media-breakpoint-up(lg) {
        margin: 20px 0;
        padding-bottom: 20px;
    }

    .gf_step {
        text-align: center;

        &:not(:last-child) {
            margin-right: 40px;
        }

        &.gf_step_active {
            .gf_step_number {
                background-color: $accent-1;
            }
        }

        .gf_step_number {
            background-color: $primary;
            border-radius: 50%;
            color: $white;
            display: block;
            font-family: $header-font;
            height: 36px;
            margin: 0 auto;
            padding: 8px;
            text-align: center;
            width: 36px;
        }

        .gf_step_label {
            display: block;
            font-family: $header-font;
        }
    }
}

/*
 * Navigation buttons
 */
.gform_page_footer {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;

    .gform_next_button,
    .gform_previous_button,
    .gform_button {
        @extend .btn-primary;

        @include media-breakpoint-down(md) {
            min-width: 150px;
        }
    }

    .gform_next_button {
        margin-left: auto;
    }
}