.module-brands {

    &.module-setting-bg-dark {
        
        .brand {
            background-color: $white;
        }

        .brand * {
            color: $black;
        }
    }

    &.module-setting-bg-white {
        
        .brand {
            background-color: #f5f5f5;
        }
    }

    &.module-setting-bg-light {

        .brand {
            background-color: $white;
        }
    }

    .brands-search-input {
        margin-bottom: 24px;
    }

    .brand {
        border-radius: 8px;
        padding: 24px;

        a {
            text-decoration: none;
        }

        .title {
            font-family: $header-font;
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 8px;
        }

        .brand-excerpt {
            font-family: $paragraph-font;
            font-size: 14px;
        }

        .brand-link {
            text-decoration: underline;
            font-family: $header-font;
            font-size: 14px;
            font-weight: 700;
            margin-top: 8px;
        }

        .brand-image {
            aspect-ratio: 1/1;
            object-fit: contain;
        }
    }

    .flex-col-3 {

        @include media-breakpoint-up(lg) {
    
            .flex-col-item {
                width: calc(33.333333% - 16px);
                margin-right: 24px;
                margin-bottom: 24px;
    
                &:nth-child(2n) {
                    margin-right: 24px;
                }
    
                &:nth-child(3n) {
                    margin-right: 0;
                }
            }
        }
    }

    .flex-col-4 {

        @include media-breakpoint-up(lg) {
            .flex-col-item {
                width: calc(25% - 18px);
                margin-right: 24px;
                margin-bottom: 24px;

                &:nth-child(2n) {
                    margin-right: 24px;
                }

                &:nth-child(3n) {
                    margin-right: 24px;
                }

                &:nth-child(4n) {
                    margin-right: 0;
                }
            }
        }
    }
}