.module-text {
	padding: 50px 0;

	@include media-breakpoint-up(lg) {
		padding: 80px 0;
	}

	.module-part-heading {
		text-align: center;
	}

		.module-part-title {
			max-width: 940px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 0;
		}

	.module-part-heading + .module-part-content {
		margin-top: 20px;

		@include media-breakpoint-up(lg) {
			margin-top: 40px;
		}
	}

	&.module-setting-columns-one {
		.module-part-content {
			max-width: 570px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	&.module-setting-columns-two {
		.module-part-content {
			@include media-breakpoint-up(lg) {
				column-count: 2;
				column-gap: 50px;
			}
		}
	}

	&.module-setting-columns-long {

		.module-part-heading {
			text-align: left;
			max-width: 860px;
			margin-left: auto;
			margin-right: auto;
		}

			.module-part-title {
				max-width: none;
			}

		.module-part-content {
			max-width: 860px;
			margin-left: auto;
			margin-right: auto;
		}

		.module-part-heading + .module-part-content {

			@include media-breakpoint-up(lg) {
				margin-top: 20px;
			}
		}
	}
}