.form-inline {

	.gform_confirmation_message {
		width: 100%;
	}

	.validation_error {
		clip: rect(1px, 1px, 1px, 1px);
		position: absolute !important;
		height: 1px;
		width: 1px;
		overflow: hidden;
	}

	.validation_message {
		text-align: left;
	}

	@include media-breakpoint-up(md) {
		display: flex;
		flex-wrap: nowrap;

		.gform_body {
			width: 100%;

			input {
				padding-top: 27px;
				padding-bottom: 27px;
				padding-left: 20px;
				padding-right: 20px;
			}
		}

		.gform_footer {
			margin: 0;
			margin-left: 20px;
		}

		.gfield {
			margin-bottom: 0;
		}
	}

	.gform_button {
		display: block;
		width: 100%;
		border-radius: 4px;
	}
}