.module-featured-product {
	padding: 50px 0 0;

	@include media-breakpoint-up(lg) {
		padding: 80px 0;
	}

	.container {
		position: relative;
	}

	.module-part-heading {}

	.module-part-pretitle {
		margin-bottom: 10px;
	}

		.module-part-title {
			margin-bottom: 10px;
		}

	.module-part-content {

		.flex {
			display: flex;
			align-items: center;

			p {
				margin: 0;
				color: $black-60;
				font-size: 16px;
			}
		}

			.flex-item {
				margin-right: 10px;

				&:last-child {
					margin-right: 0;
				}
			}

		.woocommerce .star-rating {
			float: none;
			color: $primary;

			&:before {
				color: $primary;
			}
		}

		p {
			font-size: 22px;
			color: $black;
		}
	}

	.module-part-image {

		@include media-breakpoint-down(md) {
			margin-top: 40px;

			img {
				display: block;
				width: calc(100% - 40px);
				max-width: 570px;
				height: auto;
				margin-left: auto;
				margin-right: auto;
			}
		}

		@include media-breakpoint-up(lg) {
			position: absolute;
			top: -40px;
			right: 0;
			bottom: -80px;
			width: 40%;

			img {
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
	}

	&.module-setting-bg-dark {
		.module-part-content {

			.flex p {
				color: $white-60;
			}
		}
	}

	&.module-setting-bg-brand {
		.module-part-content {

			.flex p {
				color: $white-60;
			}

			.woocommerce .star-rating {
				float: none;
				color: $black;

				&:before {
					color: $black;
				}
			}
		}
	}
}