.gform_fields {
    list-style-type: none;
    margin-bottom: 0;
    margin-top: 0;
    padding: 0;
}

.woocommerce form .form-row {
    padding: 0;
    margin: 0;
}

.woocommerce form .form-row,
.gfield {
    margin-bottom: 20px;
}

.woocommerce form .form-row label,
.gfield_label,
.gmw-form-wrapper .gmw-form-field-wrapper label {
    color: $black;
    font-family: $header-font;
    font-size: 14px;
    font-weight: 500;
    margin: 0 0 5px;
    text-align: left;
    display: block;

    .gfield_required {
        color: $error;
        margin-left: 5px;
    }
}

.ginput_container {}

.gform_footer {
    margin-top: 10px;
}

.gform_button {
    @extend .btn-primary;
}

.gform_validation_container {
    display: none !important;
    left: -9000px;
    position: absolute !important;
}

.hidden_label,
.hide-label {
    > label {
        clip: rect(1px, 1px, 1px, 1px);
        position: absolute !important;
        height: 1px;
        width: 1px;
        overflow: hidden;
    }
}

.gform_required_legend {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
}

.gform_confirmation_message {
    text-align: center;
}