.product-detail {
	padding-bottom: 30px;

	@include media-breakpoint-up(lg) {
		padding-bottom: 60px;
	}

	div.images {

		@include media-breakpoint-up(lg) {
			margin-bottom: 0 !important;
		}
	}

	> .container {
		position: relative;
	}
}

	.product-detail-footer {
		padding: 60px 0 10px;
		background: $black-5;

		@include media-breakpoint-up(lg) {
			padding-bottom: 20px;
		}
	}


.woocommerce div.product .product-detail {

	p.price, span.price, .one-time-option-details {
		font-size: 20px;
		margin-bottom: 10px;
		display: inline-block;
	}
}

small.wcsatt-sub-options {
	font-size: 100%;
}