.module-accordion {
	padding: 60px 0;

	@include media-breakpoint-up(lg) {
		padding: 90px 0;
	}

	.module-part-content {
		text-align: center;
		max-width: 720px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 20px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}

	&.module-setting-bg-dark {
		* {
			color: #fff;
		}

		.accordion__header {
			border-color: rgba(#fff, 0.05);

			path {
				fill: #fff;
			}

			&:hover,
			&:active,
			&:focus {
				color: #fff;
			}
		}
	}

}	
	
	.accordion {
		max-width: 790px;
		margin-left: auto;
		margin-right: auto;
	}

		.accordion__header {
			position: relative;
			display: block;
			width: 100%;
			background-color: transparent;
			appearance: none;
			padding: 20px 0;
			border: none;
			padding-right: 60px; // account for svg icon
			border-top: 3px solid $black-5;
			cursor: pointer;

			h3 {
				color: $black;
				font-size: 18px;
				font-family: $header-font;
				font-weight: 900;
				text-align: left;
				margin: 0;
				transition: color 0.25s;
			}

			&:first-child {
				border-top: 0;
			}

			&:hover,
			&:active,
			&:focus {
				
				h3 {
					color: $primary;
				}
			}

			svg {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 5px;
				transition: transform 0.25s;
			}

			&[aria-expanded='true'] {
				
				h3 {
					color: $primary;
				}

				svg {
					transform: translateY(-50%) rotate(-45deg);
				}
			}
		}

		.accordion__panel {
			padding: 20px 0;
		}