.coming-soon-content .gform_wrapper,
.newsletter-form {

	form {

		@include media-breakpoint-up(lg) {
			display: flex;
			align-items: center;

			.gform_body {
				width: 100%;
				max-width: 400px;
			}

				.gfield {
					margin-bottom: 0;
				}

				input[type="email"] {
					height: 52px;
				}

			.gform_footer {
				margin-top: 0;
				margin-left: 20px;
			}
		}
	}

	.gform_button {

		@include media-breakpoint-down(md) {
			display: block;
			width: 100%;
		}
	}


	input[type="email"] {
		background-image: url(../img/modules/newsletter/email-icon.svg);
		background-repeat: no-repeat;
		background-size: 20px 15px;
		background-position: left 10px center;
		padding-left: 36px !important;
	}

	.validation_error {
		clip: rect(1px, 1px, 1px, 1px);
		position: absolute !important;
		height: 1px;
		width: 1px;
		overflow: hidden;
	}

	.gform_ajax_spinner {
		display: none !important;
	}

	.gform_confirmation_message {
		text-align: center;
	}

	.gform_wrapper.gravity-theme .gform_footer, .gform_wrapper.gravity-theme .gform_page_footer {
		margin: 0;
		padding: 0;
		
		@include media-breakpoint-up(lg) {
			margin-left: 16px;
		}
	}

	.gform_footer button, .gform_footer input, .gform_page_footer button, .gform_page_footer input {
		margin-bottom: 0 !important;
	}
}