img {
	max-width: 100%;
	height: auto;
}

.alignleft {
	display: block;
	margin: 0 auto;

	@include media-breakpoint-up(lg) {
		display: inline;
		float: left;
		margin: 0 15px 0 0;
	}
}

.alignright {
	display: block;
	margin: 0 auto;

	@include media-breakpoint-up(lg) {
		display: inline;
		float: right;
		margin: 0 0 0 15px;
	}
}

.aligncenter {
	clear: both;
	display: block;
	margin: 0 auto;
}
