.modal[aria-hidden='true'] {
	opacity: 0;
	pointer-events: none;
	display: none;
}

.modal {
	position: fixed;
	z-index: 9000;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	opacity: 1;
	pointer-events: auto;
	transition: opacity 0.25s;
}

	.modal-backdrop {
		position: absolute;
		z-index: +10;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba($black, 0.9);
	}

	.modal-dialog {
		position: relative;
		z-index: +15;
		max-width: 900px;
		width: 90vw;
	}

		.modal-content {}

		.modal-header {
			position: fixed;
			top: 20px;
			right: 20px;
			z-index: +5;
		}


.modal-team {

	.modal-header {
		top: -15px;
		right: -15px;

		button {
			padding: 0;
			width: 50px;
			height: 50px;
			background-color: #fff;
			border-radius: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			box-shadow: $shadow-3;

			&:hover,
			&:active,
			&:focus {
				
				path {
					fill: $black-60;
				}
			}

			svg {
				display: inline-block;
				transform: scale(0.8);
			}

			path {
				fill: $black;
				transition: fill 0.25s;
			}
		}
	}

	.modal-dialog {
		position: absolute;
		background-color: #fff;
		height: calc(100% - 60px);
		width: calc(100vw - 40px);
		max-height: 700px;
		box-shadow: 0 0 7px rgba(0,0,0,0.2);
		overflow: visible;
		max-width: 400px;
		top: 50%;
		transform: translateY(-50%);
		margin-left: auto;
		margin-right: auto;
		left: 0;
		right: 0;
		
		@include media-breakpoint-up(lg) {
			// display: flex;
			max-width: 1080px;
		}
	}

		.modal-image {
			position: relative;
			width: 100%;
			height: 38%;

			img {
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				width: 100%;
				height: 100%;
			}

			@include media-breakpoint-up(lg) {
				height: 100%;
				width: 50%;
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
			}
		}

		.modal-content {
			position: absolute;
			top: 38%;
			left: 0;
			right: 0;
			bottom: 0;
			padding: 40px 20px;
			text-align: center;
			overflow: scroll;
			-webkit-overflow-scrolling: touch;

			@include media-breakpoint-up(lg) {
				padding: 70px 80px;
				height: 100%;
				width: 50%;
				position: absolute;
				top: 0;
				right: 0;
				left: auto;
				bottom: 0;
			}
		}
}

.modal-popup {

	.modal-dialog {
		background: #fff;
		padding: 16px;

		@include media-breakpoint-up(lg) {
			padding: 24px;
		}

		iframe {
			width: 100%;
			height: 80vh;
			margin: 0;
			padding: 0;
		}
	}
}