.post-detail {
	max-width: 790px;
	margin-left: auto;
	margin-right: auto;
	padding: 60px 0;

	@include media-breakpoint-up(lg) {
		padding: 120px 0;
	}
}

	.post-detail-header {
		padding-bottom: 40px;

		p {
			margin-bottom: 0;
		}

		h1 {
			margin-bottom: 0;
		}
	}

	.post-detail-featured-image {
		padding: 30px 0;

		img {
			display: inline-block;
			max-width: 100%;
			height: auto;
		}
	}

	.post-detail-content {

		figure {
			margin: 0;
			margin-bottom: 30px;
			width: auto !important;
			max-width: 100%;
		}

	}

	.post-detail-footer {
		margin-top: 30px;
		text-align: center;

		.flex {
			@include media-breakpoint-up(lg) {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
		}

		h3 {
			font-size: 20px;
			margin-bottom: 30px;

			@include media-breakpoint-up(lg) {
				margin-bottom: 0;
			}
		}
	}

	.post-detail-related {
		background-color: $black-5;
		padding: 60px 0;

		@include media-breakpoint-up(lg) {
			padding: 90px 0 40px;
		}
	}

		.post-detail-related-header {
			text-align: center;

			h2 {
				font-size: 30px;
				margin-bottom: 40px;

				@include media-breakpoint-up(lg) {
					font-size: 50px;
				}
			}
		}