.module-gallery {
	padding: 60px 0 36px; // subtract 24px from bottom padding because of margin bottom on gallery items

	@include media-breakpoint-up(lg) {
		padding: 90px 0 66px;
	}

	.module-part-heading {
		max-width: 640px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		margin-bottom: 20px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}

	.module-part-content {
		max-width: 620px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 35px;
		text-align: center;
	}

	.gallery-image {
		position: relative;
		overflow: hidden;

		a {
			display: block;

			img {
				display: block;
				transition: opacity 0.25s;
			}

			&:after {
				content: "";
				position: absolute;
				z-index: +3;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: rgba($black, 0.3);
				opacity: 0;
				transition: opacity 0.25s;
			}

			&:before {
				content: "";
				position: absolute;
				z-index: +5;
				top: 50%;
				transform: translateY(-50%);
				width: 57px;
				height: 49px;
				background-image: url(../img/camera.svg);
				display: block;
				background-size: 57px 49px;
				margin-left: auto;
				margin-right: auto;
				left: 0;
				right: 0;
				opacity: 0;
				transition: opacity 0.25s;
			}

			&:hover,
			&:active,
			&:focus {

				&:before,
				&:after {
					opacity: 0.99;
				}
			}
		}
	}
	
	// grid style galleries
	&.grid {

		.gallery {
			display: flex;
            flex-wrap: wrap;
		}

		.gallery-image {
			width: calc(50% - 13px);
			margin: 0;
			margin-right: 24px;
			margin-bottom: 24px;

			&:nth-child(2n) {
				margin-right: 0;
			}

			a {
				position: relative;
				padding-bottom: 100%;
			}

			@include media-breakpoint-up(md) {
				width: calc(33.3333333% - 17px);

				&:nth-child(2n) {
					margin-right: 24px;
				}

				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			@include media-breakpoint-up(lg) {
				width: calc(25% - 19px);

				&:nth-child(3n) {
					margin-right: 24px;
				}

				&:nth-child(4n) {
					margin-right: 0;
				}
			}

			img {
				display: block;
				height: 100%;
				object-fit: cover;
				object-position: 50% 50%;
				width: 100%;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
			}

			figcaption {
				text-align: center;
				margin-bottom: 0;
				margin-top: 8px;
				line-height: 1.2;
			}
		}
	}
	
	// masonry style galleries
	&.masonry {

		.grid-sizer {
			width: 50%;

			@include media-breakpoint-up(lg) {
				width: 33.333333%;
			}
		}

		.gallery {}

		.gallery-image,
		.grid-sizer {
			margin: 0;
			padding: 0;
			width: calc(50% - 12px);

			@include media-breakpoint-up(lg) {
				width: calc(33.333333% - 16px);
			}
		}

		.gallery-image {
			margin-bottom: 24px;
		}

	}

	.module-part-logos {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		margin-bottom: -30px;

		.flex-item {
			width: 33.333333%;
			margin-bottom: 30px;
			padding: 0 15px;

			@include media-breakpoint-up(sm) {
				width: auto;
			}

			@include media-breakpoint-up(lg) {
				padding: 0 30px;
			}

			a {
				display: inline-block;
			}

			img {
				display: inline-block;
				height: auto;
				max-width: 100%;
			}
		}
	}
}
