/*
 * Single File Upload
 */
.ginput_container_fileupload:not(.multi-upload) {
    &::before,
    input[type="file"] {
        display: inline-block;
        vertical-align: middle;
    }

    &::before {
        content: url('../img/forms/file-icon.svg');
        margin-right: 20px;
    }

    #extensions_message {
        font-family: $header-font;
        display: block;
        margin-top: 15px;

        @include media-breakpoint-up(lg) {
            margin-top: 20px;
        }
    }
}

/*
 * Multi File Upload
 */

.gform_fileupload_multifile .gform_drop_area {
    padding: 30px;
    border: 1px dashed $black-25;
    text-align: center;
    border-radius: 4px;

    .gform_drop_instructions,
    .gform_drop_instructions::before,
    .gform_button_select_files {
        display: inline-block;
        vertical-align: middle;
    }

    .gform_drop_instructions {
        font-family: $header-font;

        &::before {
            content: url('../img/forms/file-icon.svg');
            margin-right: 20px;
        }
    }

    .gform_button_select_files {
        @extend .btn-accent-1;

        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 15px;
    }
}