.ginput_container_name {
    @include make-row;
    
    .name_prefix,
    .name_first,
    .name_middle,
    .name_last,
    .name_suffix {
        @include make-col-ready;
    
        @include media-breakpoint-up(lg) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
        }

        label {
            color: $black-60;
            display: block;
            font-family: $header-font;
            font-size: 13px;
            margin-bottom: 5px;
        }
    }

    .name_prefix,
    .name_suffix {
        @include media-breakpoint-up(lg) {
            flex: 0 0 10%;
        }
    }

    .name_prefix select {
        -moz-appearance: menulist-button;
        -webkit-appearance: menulist-button;
    }

    span:not(:first-child) {
        @include media-breakpoint-down(md) {
            margin-top: 15px;
        }
    }
}

.field_sublabel_above {
    .ginput_container_name {
        span label {
            margin-bottom: 5px;
        }
    }
}

.field_sublabel_below {
    .ginput_container_name {
        span label {
            margin-top: 5px;
        }
    }
}
