.module-video {
	
	.module-part-content {
		position: relative;
		overflow: hidden;

		&:before {
			content: "";
			position: absolute;
			z-index: +10;
			background-color: rgba(58, 51, 53, 0.3);
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
		}

		img {
			position: relative;
			z-index: +5;
			display: block;
			width: 100%;
			height: 44vw;
			max-height: 620px;
			min-height: 370px;
		}

		button {
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
			z-index: +15;
		}
	}
}

.module-image {
	img {
		display: block;
		width: 100%;
		height: 44vw;
		max-height: 666px;
		min-height: 370px;
	}
}