.flex-col {
    margin-bottom: -20px;

    .flex-col-item {
        margin-bottom: 20px;
        width: 100%;
    }
}

.flex-col-center {
    justify-content: center;

    .flex-col-item:last-child {
        margin-right: 0 !important;
    }
}

.flex-col-2 {

    @include media-breakpoint-up(md) {
        display: flex;
        flex-wrap: wrap;

        .flex-col-item {
            width: calc(50% - 20px);
            margin-right: 40px;
            margin-bottom: 40px;

            &:nth-child(2n) {
                margin-right: 0;
            }
        }
    }
}

.flex-col-3 {

    @include media-breakpoint-up(lg) {
        display: flex;
        flex-wrap: wrap;

        .flex-col-item {
            width: calc(33.333333% - 26.666666667px);
            margin-right: 40px;
            margin-bottom: 40px;

            &:nth-child(2n) {
                margin-right: 40px;
            }

            &:nth-child(3n) {
                margin-right: 0;
            }
        }
    }
}

.flex-col-4 {

    @include media-breakpoint-up(xl) {
        display: flex;
        flex-wrap: wrap;

        .flex-col-item {
            width: calc(25% - 30px);
            margin-right: 40px;
            margin-bottom: 40px;

            &:nth-child(2n) {
                margin-right: 40px;
            }

            &:nth-child(3n) {
                margin-right: 40px;
            }

            &:nth-child(4n) {
                margin-right: 0;
            }
        }
    }
}