/*
 * Date field
 */
.ginput_container_date {
    position: relative;

    .ui-datepicker-trigger {
        cursor: pointer;
        position: absolute;
        right: 30px;
        top: 50%;
        transform: translateY(-50%);
    }
}

/*
 * Date picker
 */
.ui-datepicker {
    background-color: $white;
    border-radius: 4px;
    box-shadow: $shadow-3;
    display: none;
    padding: 20px;

    .ui-datepicker-header {
        align-items: center;
        display: flex;
        justify-content: space-around;

        .ui-datepicker-prev,
        .ui-datepicker-next {
            top: auto;
            height: auto;
            width: auto;

            span {
                display: none;
            }

            &::after {
                content: url('../img/forms/datepicker-arrow.svg');
                cursor: pointer;
                display: block;
            }
        }

        .ui-datepicker-prev {
            order: 1;

            &::after {
                transform: rotate(180deg);
            }
        }

        .ui-datepicker-title {
            display: flex;
            margin: 0 10px;
            order: 2;

            select {
                width: 85px;
            }

            .ui-datepicker-month {
                margin-right: 10px;
            }
        }

        .ui-datepicker-next {
            order: 3;
        }
    }

    .ui-datepicker-calendar {
        margin-top: 25px;
        width: 100%;

        th {
            span {
                color: $black;
                font-family: $header-font;
                font-weight: 700;
            }
        }

        td {
            // padding: 10px;
            text-align: center;

            &.ui-datepicker-today a {
                color: $accent-1;
            }

            a {
                color: $black;
                font-family: $header-font;
                text-decoration: none;
            }
        }
    }
}
