.module-newsletter {
	
	.flex {

		@include media-breakpoint-up(lg) {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

		.module-part-content {
			text-align: center;

			@include media-breakpoint-up(lg) {
				text-align: left;
			}
		}

			.module-part-title {
				margin-bottom: 10px;

				@include media-breakpoint-up(lg) {
					font-size: 36px;
				}
			}

		.module-part-form {
			margin-top: 30px;

			@include media-breakpoint-up(lg) {
				margin-top: 0;
				margin-left: 30px;
				min-width: 600px;
			}
		}

	&.module-setting-bg-white {

		input[type="email"] {
			background-color: rgba($black, 0.05);
			border: none;
		}
	}

}