.age-gate-logo {
	width: auto;
	height: auto;
	max-height: 300px;
	max-width: 300px;
	margin-bottom: 20px;
}

.age-gate-submit-yes {
	@extend .btn-primary;
}

.age-gate-submit-no {
	@extend .btn-primary-outline;
	color: $primary;

	&:hover,
	&:active,
	&:focus {
		color: #fff;
	}
}

.age-gate-submit-yes,
.age-gate-submit-no {
	min-width: 120px;
}