.pagination .nav-links {
	text-align: center;
	font-size: 16px;

	a, span {
		text-decoration: none;
		color: $black-60;
		display: inline-block;
		width: 30px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		vertical-align: middle;
		border-radius: 3px;

		&.current {
			background-color: $primary;
			color: #fff;
		}
	}

	a {
		transition: background-color 0.25s;

		&:hover,
		&:active,
		&:focus {
			background-color: $black-5;
		}
	}

	.current {
		color: $black-60;
	}

	.prev, .next {
		text-indent: -9999px;
		position: relative;
		background-image: url(../img/left.svg);
		background-size: 10px 15px;
		background-position: center;
		background-repeat: no-repeat;
	}

	.next {
		background-image: url(../img/right.svg);
	}
}

.woocommerce nav.woocommerce-pagination {

	ul.page-numbers {
		border: none;
		margin: 0;
		padding: 0;

		&:before,
		&:after {
			display: none;
		}

		li {
			border: none;
			margin-right: 3px;

			&:last-child {
				margin-right: 0;
			}

			a, span {
				padding: 0;
				text-decoration: none;
				color: $black-60;
				display: inline-block;
				width: 30px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				vertical-align: middle;
				border-radius: 3px;

				&.current {
					background-color: $primary;
					color: #fff;
				}

				&.prev, &.next {
					text-indent: -9999px;
					position: relative;
					background-image: url(../img/left.svg);
					background-size: 10px 15px;
					background-position: center;
					background-repeat: no-repeat;
				}

				&.next {
					background-image: url(../img/right.svg);
				}
			}
		}
	}
}