.store-locator-content {
	padding: 30px 0 0;
	margin-bottom: -30px;
}

.gmw-form-wrapper {
	margin: 0;
	padding: 30px 0;
	// background-color: $black-5;

	.gmw-form {

		@include media-breakpoint-up(lg) {
			display: flex;
			// align-items: flex-start;
			width: 100%;
			justify-content: center;
		}
	}
}

.gmw-form-wrapper .gmw-form-field-wrapper label {

	@include media-breakpoint-down(md) {
		margin: 15px 0 5px;
	}
}

.gmw-address-field-wrapper {

	@include media-breakpoint-up(lg) {
		width: 100%;
	}
}

div.gmw-form-wrapper .gmw-submit, div.gmw-form-wrapper input[type=submit] {
	@extend .btn-primary;
	min-width: 0;
	padding: 11.5px 25px;

	@include media-breakpoint-down(md) {
		width: 100%;
		display: block;
		padding-top: 14px;
		padding-bottom: 14px;
	}
}

.gmw-form-wrapper .gmw-form-field-wrapper {
	margin: 0 0 20px;

	@include media-breakpoint-up(lg) {
		margin-right: 30px;
		margin-bottom: 0;

		&:last-child {
			margin-right: 0;
		}
	}
}

.gmw-search-form-taxonomies {

	.gmw-form-field-wrapper {
		margin-bottom: 20px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
			margin-right: 30px !important;
			min-width: 200px;
		}
	}
}

.gmw-distance-field-wrapper {

	@include media-breakpoint-up(lg) {
		min-width: 100px;
	}
}

.gmw-submit-field-wrapper {

	@include media-breakpoint-down(md) {
		margin-bottom: 0 !important;
	}

	@include media-breakpoint-up(lg) {
		display: flex;
		align-items: flex-end;
		margin-right: 0 !important;
	}
}

.gmw-location-meta-wrapper {

	h3 {
		display: none;
	}
}

div.gmw-form-wrapper .gmw-submit, div.gmw-form-wrapper input[type=submit] {
	margin: 0;
}

.gmw-results-wrapper {
	background-color: $black-5;
	padding: 30px;

	@include media-breakpoint-up(lg) {
		padding: 60px;
	}

	.flex {

		@include media-breakpoint-up(lg) {
			display: flex;
		}
	}

		.flex-item {

			&:first-child {
				background-color: #fff;

				@include media-breakpoint-up(lg) {
					width: 40%;
				}
			}

			&:last-child {

				@include media-breakpoint-up(lg) {
					width: 60%;
				}

				.gmw-map-wrapper {

					@include media-breakpoint-up(lg) {
						height: 100% !important;
					}
				}
			}
		}
}

	.gmw-results {
		padding: 30px;

		@include media-breakpoint-up(lg) {
			height: calc(100vh - 200px);
			overflow: scroll;
			padding: 40px;
		}
	}

		.gmw-results-count {
			color: $black-60;
			font-size: 14px;
		}

		.gmw-posts-wrapper {
			margin: 0;
			margin-top: 30px;
			padding: 0;
			list-style-type: none;

			> li {
				border-bottom: 1px solid rgba($black, 0.1);
				padding-bottom: 30px;
				margin-bottom: 30px;

				&:last-child {
					border-bottom: none;
					margin-bottom: 0;
					padding-bottom: 0;
				}

				h3 {
					margin-bottom: 10px;

					span {
						color: $black-60;
						font-size: 80%;
						display: inline-block;
						margin-left: 5px;
					}
				}
			}
		}