.gform_card_icon_container {
    height: 32px;
    margin-bottom: 15px;

    @include media-breakpoint-up(lg) {
        margin-bottom: 20px;
    }
}

.gform_card_icon {
    background-image: url('../img/forms/gf-creditcard-icons.png');
    background-repeat: no-repeat;
    float: left;
    height: 32px;
    margin-right: 4px;
    text-indent: -9000px;
    width: 36px;
}

