/*
 * Credit card fields
 */
.ginput_container_creditcard {
     > span {
        margin-top: 15px;
        display: block;
    }

    label {
        color: $black-60;
        display: block;
        font-family: $header-font;
        font-size: 13px;
        margin-bottom: 5px;
    }

    .ginput_cardextras {
        @include make-row;

        .ginput_cardinfo_left {
            @include make-col-ready;

            @include media-breakpoint-up(lg) {
                @include make-col(8);
            }

            .ginput_card_expiration_container {
                @include media-breakpoint-up(lg) {
                    @include make-row;
                }

                &::after,
                &::before {
                    background-image: url('../img/forms/select-chevron.svg');
                    background-repeat: no-repeat;
                    content: '';
                    display: block;
                    height: 7px;
                    pointer-events: none;
                    position: absolute;
                    width: 12px;
                }

                &::after {
                    right: 35px;
                    bottom: 17px;

                    @include media-breakpoint-up(lg) {
                        top: 50%;
                        transform: translateY(-50%) translateY(12px);
                        right: 35px;
                        bottom: initial;
                    }
                }

                &::before {
                    right: 35px;
                    top: 42px;

                    @include media-breakpoint-up(lg) {
                        top: 50%;
                        transform: translateY(-50%) translateY(12px);
                        right: auto;
                        left: calc(50% - 45px);
                    }
                }

                label {
                    @include media-breakpoint-up(lg) {
                        @include make-col-ready;
                    }
                }

                select {
                    @include media-breakpoint-up(lg) {
                        width: calc(50% - #{$grid-gutter-width});
                        margin-left: $grid-gutter-width / 2;
                        margin-right: $grid-gutter-width / 2;
                    }

                    &.ginput_card_expiration_year {
                        @include media-breakpoint-down(md) {
                            margin-top: 20px;
                        }
                    }
                }
            }
        }

        .ginput_cardinfo_right {
            @include make-col-ready;

            align-items: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            @include media-breakpoint-up(lg) {
                @include make-col(4);
            }

            @include media-breakpoint-down(md) {
                margin-top: 15px;
            }

            label {
                @include make-col(12);
            }

            .ginput_card_security_code {
                width: calc(100% - 60px);
            }

            .ginput_card_security_code_icon {
                background-image: url('../img/forms/gf-creditcard-icons.png');
                background-position: 0 -128px;
                background-repeat: no-repeat;
                display: inline-block;
                height: 23px;
                left: 6px;
                position: relative;
                top: -1px;
                width: 32px;
                margin-right: 7.5px;
            }
        }
    }
}

.field_sublabel_above {
    .ginput_container_creditcard {
        span label {
            margin-bottom: 5px;
        }
    }
}

.field_sublabel_below {
    .ginput_container_creditcard {
        span label {
            margin-top: 5px;
        }
    }
}

