.woocommerce-products-header {
	display: none !important;
}


.product-list-wrap {
	@extend .container;
	padding-top: 0;
	padding-bottom: 30px;
	display: flex;
	flex-direction: column-reverse;

	@include media-breakpoint-up(lg) {
		padding-top: 10px;
		padding-bottom: 60px;
		flex-direction: row-reverse;
	}
}

	.product-list {
		width: 100%;
	}

	.product-list-sidebar {

		.widget h3 {
			text-transform: uppercase;
			font-size: 16px;
			font-weight: bold;
		}

		.widget-area {
			display: none;
		}

		@include media-breakpoint-up(lg) {
			width: 200px;
			min-width: 200px;
			margin-right: 40px;

			.widget-area {
				display: block !important;
			}
		}

		.widget {
			margin-bottom: 30px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

		.product-list-filter-toggle {
			display: block;
			width: 100%;
			color: $black;
			border: none;
			appearance: none;
			padding: 16px 32px;
			text-align: center;
			background-color: $black-5;
			transition: background-color 0.25s;
			margin-bottom: 16px;

			&:active,
			&:hover,
			&:focus {
				background-color: $black-10;
			}

			@include media-breakpoint-up(lg) {
				display: none;
			}
		}

	.woocommerce .products ul, .woocommerce ul.products {

		@include media-breakpoint-up(sm) {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
			align-items: stretch;
		}

		&:before,
		&:after {
			display: none;
		}

		li.product {
			clear: none;
			float: none !important;
			width: 100% !important;
			max-width: 350px;
			margin: 0 auto 30px !important;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding: 20px;
			box-shadow: 0 2px 9px 0 rgba(0,0,0,.1);
			background: #fff;
			margin: 0;
			border-radius: 4px;

			.woocommerce-loop-product__link {
				display: flex;
				width: 100%;
				flex-direction: column;

				&:hover,
				&:active,
				&:focus {
					
					h2 {
						color: $primary;
					}
				}

				img, h2, .price {
					order: 1;
				}

				.jdgm-widget {
					order: 1;
					margin-bottom: 5px;
				}

				h2 {
					transition: color 0.25s;
				}

				.star-rating {
					order: 2;
					margin-bottom: 30px;
				}

				.price {
					font-size: 18px;
				}
			}

			.button {
				@extend .btn-black-ghost;
				background: transparent;
				min-width: 0;
				padding: 20px;
			}

			.added_to_cart {
				display: block;
				color: $black;
				width: 100%;
				text-align: center;
				font-family: $header-font;
				letter-spacing: 1px;
				text-transform: uppercase;
				transition: color 0.25s;
				font-size: 14px;

				&:hover,
				&:active,
				&:focus {
					color: $primary;
				}
			}
		}

		&.columns-2 {

			li.product {
				@include media-breakpoint-up(sm) {
					max-width: none;
					width: calc(50% - 15px) !important;
					margin-left: 0 !important;
					margin-right: 30px !important;

					&:nth-child(2n) {
						margin-right: 0 !important;
					}
				}
			}
		}

		&.columns-3 {

			li.product {
				@include media-breakpoint-up(sm) {
					width: calc(50% - 15px) !important;
					margin-right: 30px !important;
					margin-left: 0 !important;
					max-width: none;

					&:nth-child(2n) {
						margin-right: 0 !important;
					}
				}

				@include media-breakpoint-up(md) {
					width: calc(33.333333% - 20px) !important;

					&:nth-child(2n) {
						margin-right: 30px !important;
					}

					&:nth-child(3n) {
						margin-right: 0 !important;
					}
				}
			}
		}

		&.columns-4 {

			li.product {
				@include media-breakpoint-up(sm) {
					width: calc(50% - 15px) !important;
					margin-right: 30px !important;
					margin-left: 0 !important;
					max-width: none;

					&:nth-child(2n) {
						margin-right: 0 !important;
					}
				}

				@include media-breakpoint-up(md) {
					width: calc(33.333333% - 20px) !important;

					&:nth-child(2n) {
						margin-right: 30px !important;
					}

					&:nth-child(3n) {
						margin-right: 0 !important;
					}
				}

				@include media-breakpoint-up(lg) {
					width: calc(25% - 22.5px) !important;

					&:nth-child(3n) {
						margin-right: 30px !important;
					}

					&:nth-child(4n) {
						margin-right: 0 !important;
					}
				}
			}
		}
	}


.widget_product_search {

	form {
		position: relative;
	}

	button[type="submit"] {
		padding: 0;
		background-color: transparent;
		border: none;
		appearance: none;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 10px;
		text-indent: -9999px;
		background-image: url(../img/forms/search.svg);
		background-size: cover;
		width: 18px;
		height: 18px;
		transition: opacity 0.25s;

		&:hover,
		&:active,
		&:focus {
			opacity: 0.6;
		}
	}
}