.site-search {

	.modal-dialog {
		max-width: 720px;
	}

	.modal-content {
		background-color: #fff;
		padding: 30px;

		@include media-breakpoint-up(lg) {
			padding: 60px;
		}

		h1 {
			text-align: center;
		}
	}
}

	.site-search-form {

		input[type="search"] {
			height: 56px;
		}

		button {
			display: block;
			width: 100%;
			margin-top: 30px;
		}

		@include media-breakpoint-up(md) {
			display: flex;
			align-items: center;

			button {
				margin-top: 0;
				width: auto;
				margin-left: 30px;
			}
		}
	}