.clear-multi {
	display: flex;

	.gfield_time_hour,
	.gfield_time_minute {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		label,
		i {
			font-family: $header-font;
		}

		label {
			width: 100%;
		}

		i {
			margin: 0 10px;
		}
	}

	.gfield_time_hour {
		input {
			width: calc(100% - 25px);
		}
	}

	.gfield_time_minute {
		margin-right: 20px;
	}

	.gfield_time_ampm select {
		-moz-appearance: menulist-button;
		-webkit-appearance: menulist-button;
	}
}