.module-instagram {

	.container-fluid {
		padding-left: 15px;
		padding-right: 25px;

		@media(min-width: $nav-breakpoint) {
			padding-left: 50px;
			padding-right: 50px;
		}
	}

	img {
		margin: 0 10px;
		display: block;
	}

	#sb_instagram {
		padding-bottom: 0 !important;
	}

	.sb_instagram_header {
		padding: 0 !important;
	}

	#sbi_images {
		margin-left: -20px;
		margin-right: -20px;
		margin-bottom: -20px;
		width: calc(100% + 40px) !important;
	}
}