.social-share {
	list-style-type: none;
	margin: 0;
	padding: 0;
	display: flex;
	align-items: center;
	justify-content: center;

	li {
		position: relative;
		margin-right: 20px;

		&:last-child {
			margin-right: 0;
		}

		a, button {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 32px;
			height: 32px;
			background-color: $black-60;
			border-radius: 100%;
			border: none;
			transition: background-color 0.25s;
			padding: 0;

			&:hover,
			&:active,
			&:focus {
				background-color: $black;
			}
		}
	}
}

	.social-share-tooltip {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: calc(100% + 5px);
		white-space: nowrap;
		background-color: $black;
		padding: 5px 8px;
		border-radius: 3px;
		font-size: 12px;
		color: #fff;
		opacity: 0;
		pointer-events: none;
		transition: opacity 0.25s;

		&.is-open {
			opacity: 1;
		}

		&:after {
			content: "";
			position: absolute;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 4px 0 4px 5px;
			border-color: transparent transparent transparent $black;
			right: -5px;
			top: 50%;
			transform: translateY(-50%);
		}
	}