.site-footer {
	padding: 60px 0;
	background-color: $black;
	border-bottom: 2px solid rgba(#fff, 0.1);

	@include media-breakpoint-up(lg) {
		padding: 90px 0;
	}

	.flex {
		@include media-breakpoint-up(lg) {
			display: flex;
			justify-content: space-between;
		}
	}

	.footer-item {
		margin-bottom: 30px;
		text-align: center;

		&:last-child {
			margin-bottom: 0;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
			text-align: left;
		}

		h4 {
			color: #fff;
			margin-bottom: 10px;
		}

		p, ul {
			color: #fff;
			list-style: none;
			margin-left: 0;
			padding-left: 0;

			&:last-child {
				margin-bottom: 0;
			}

			a {
				color: #fff;
				transition: opacity 0.25s;

				&:hover,
				&:active,
				&:focus {
					opacity: 0.6;
				}
			}
		}

		.gform_button {
			padding: 14px 24px !important;
			height: auto !important;
			display: block;
			width: 100%;
			line-height: 1;
			margin-bottom: 0 !important;
			border-radius: 4px;

			@include media-breakpoint-up(lg) {
				width: auto;
				min-width: 0;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}

		.gform_footer {
			margin: 0 !important;
			padding: 0 !important;
		}

		.gform_body,
		.gfield {

			@include media-breakpoint-up(lg) {
				margin: 0 !important;
				padding: 0 !important;
				width: 100%;

				input[type=email] {
					border-top-right-radius: 0;
					border-bottom-right-radius: 0;
					height: 42px;
				}
			}
		}

		.gform_wrapper form {
			margin-bottom: 24px;

			@include media-breakpoint-up(lg) {
				display: flex;
				align-items: center;
			}
		}
	}
}

	.footer-social-links {
		margin: 0;
		padding: 0;
		list-style-type: none;
		display: flex;
		align-items: center;
		justify-content: center;

		@include media-breakpoint-up(lg) {
			justify-content: flex-start;
		}

		li {
			margin-right: 12px;

			&:last-child {
				margin-right: 0;
			}

			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 40px;
				height: 40px;
				border: 2px solid #fff;
				border-radius: 100%;
				transition: background-color 0.25s;

				path {
					transition: fill 0.25s;
				}

				&:hover,
				&:active,
				&:focus {
					background-color: #fff;

					path {
						fill: $black;
					}
				}
			}
		}
	}

.site-attribution {
	padding: 30px 0;
	background-color: $black;
	text-align: center;

	.flex {
		display: flex;
		flex-direction: column;
		align-items: center;

		@include media-breakpoint-up(lg) {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
		}
	}

		.flex-item {
			display: flex;
			align-items: center;
			flex-direction: column;

			@include media-breakpoint-up(lg) {
				flex-direction: row;
			}

			p {
				margin: 0;

				@include media-breakpoint-up(md) {
					margin-right: 5px;
				}
			}
		}

			.footer-separator {
				display: none;

				@include media-breakpoint-up(lg) {
					display: inline-block;
					opacity: 0.6;
				}
			}

	p {
		color: rgba(#fff, 0.6);

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
		}

		a {
			color: rgba(#fff, 0.6);

			&:hover,
			&:active,
			&:focus {
				color: #fff;
			}
		}
	}
}

	.site-policies-menu {
		margin: 0;
		padding: 0;
		list-style-type: none;

		@include media-breakpoint-up(lg) {
			display: flex;
			align-items: center;
		}

		li {

			@include media-breakpoint-up(lg) {
				margin-right: 10px;

				&:last-child {
					margin-right: 0;
				}
			}

			a {
				color: $white-60;
				transition: opacity 0.25s;

				&:hover,
				&:active,
				&:focus {
					opacity: 0.6;
				}
			}
		}
	}

	.site-footer-disclaimer {
		text-align: left;
		margin-bottom: 30px;

		p {
			font-size: 80%;
			margin-bottom: 20px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.site-by {
		display: flex;
		align-items: center;
		margin-top: 30px;
		margin-bottom: 0;

		@include media-breakpoint-up(lg) {
			margin-top: 0;
		}

		a {

			&:hover,
			&:active,
			&:focus {
				
				path {
					fill: #F04E38;
				}

				circle {
					opacity: 1;
				}

				svg {
					animation-name: rotate; 
					animation-duration: 2s; 
					animation-iteration-count: infinite;
					animation-timing-function: linear;
				}
			}
		}

		span, svg {
			display: block;
		}

		svg {
			margin-left: 10px;
			transition-property: transform;
			transition-duration: 2s;

			circle {
				opacity: 0;
				transition: opacity 0.25s;
			}

			path {
				fill: $white-60;
				transition: fill 0.25s;
			}
		}
	}