.module-tabs {
	padding: 60px 0;

	@include media-breakpoint-up(lg) {
		padding: 90px 0;
	}

	&.module-setting-bg-dark {

		@include media-breakpoint-up(lg) {
			.tabs-nav {
				border-color: rgba(#fff, 0.05);
			}
			
			.tabs-nav .tabs-nav-item.active a {
				color: #fff;
			}
			.tabs-nav .tabs-nav-item a {
				color: rgba(#fff, 0.4);
			}

			.tabs-nav .tabs-nav-item.active {
				border-color: #fff;
			}
		}
		
	}

	.module-part-content {}

		.module-part-text {
			text-align: center;
			max-width: 720px;
			margin-left: auto;
			margin-right: auto;
		}

		.module-part-content-wrap {
			max-width: 790px;
			margin-left: auto;
			margin-right: auto;
		}

	.tabs-dropdown {
		border-radius: 4px;
		box-shadow: $shadow-3;

		@include media-breakpoint-up(lg) {
			box-shadow: none;
		}

		&.open {
			.tabs-nav {
				display: block;
			}

			.tabs-dropdown-toggle {
				.chevron {
					transform: translateY(-50%) rotate(180deg);
				}
			}
		}
	}

	.tabs-dropdown-toggle {
		background-color: $white;
		border: 0;
		border-radius: 4px;
		cursor: pointer;
		padding: 20px;
		position: relative;
		text-align: left;
		width: 100%;

		@include media-breakpoint-up(lg) {
			display: none;
		}

		&::after {
			content: 'mobile-navigation';
			display: none;

			@include media-breakpoint-up(lg) {
				content: 'desktop-navigation';
			}
		}

		.active {
			font-family: $header-font;
			font-size: 13px;
			text-transform: uppercase;
			color: $black;
		}

		.chevron {
			position: absolute;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			transition: transform 0.3s;
		}
	}

	.tabs-nav {
		border-radius: 0 0 4px 4px;
		display: none;
		list-style: none;
		margin: 0;
		padding: 10px 0;
		background-color: #fff;

		@include media-breakpoint-up(lg) {
			border-bottom: 3px solid $black-5;
			display: flex;
			justify-content: space-between;
			flex-wrap: nowrap;
			padding: 0;
			background-color: transparent;
		}

		.tabs-nav-item {
			@include media-breakpoint-up(lg) {
				border-bottom: 8px solid transparent;
			}

			&.active {
				@include media-breakpoint-up(lg) {
					border-bottom: 8px solid $primary;
				}

				a {
					color: $black;
				}
			}
		}

		.tabs-nav-item-link {
			color: rgba($black, 0.6);
			display: block;
			font-family: $header-font;
			font-size: 13px;
			padding: 10px 20px;
			text-decoration: none;
			font-weight: 500;
			text-transform: uppercase;

			@include media-breakpoint-up(lg) {
				padding: 28px 30px 20px;
			}
		}
	}

	.tabs-content {
		padding-top: 40px;

		@include media-breakpoint-up(lg) {
			padding-top: 60px;
		}

		> .tabs-content-pane {
			display: none;
		}

		> .active {
			display: block;
		}
	}
}
