body {
	font-family: $paragraph-font;
	-webkit-font-smoothing: antialiased;
	font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

p,
ol,
ul,
figcaption {
	margin-top: 0;
	font-family: $paragraph-font;
	font-size: $paragraph-font-size;
	font-weight: $paragraph-font-weight;
	line-height: 1.875;
	color: rgba($black, 0.6);
	margin-bottom: 30px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $header-font;
	font-weight: 700;
	margin-top: 0;
	color: $black;
	line-height: 1.2;
	margin-bottom: 20px;
}

h1, .h1 {
	font-size: 28px;
	
	@include media-breakpoint-up(lg) {
		font-size: 44px;
	}
}

h2, .h2 {
	font-size: 22px;

	@include media-breakpoint-up(lg) {
		font-size: 38px;
	}
}

h3, .h3 {
	font-size: 18px;

	@include media-breakpoint-up(lg) {
		font-size: 22px;
	}
}

h4, .h4 {
	font-size: 16px;

	@include media-breakpoint-up(lg) {
		font-size: 18px;
	}
}

h5, h6, .h5, .h6 {
	font-size: 16px;
}

button {
	cursor: pointer;
}


// styles for text links
a {
	text-decoration: none;
	color: $primary;
	transition: color 0.25s;

	&:hover,
	&:active,
	&:focus {
		color: $primary-dark;
	}
}