.site-notice {
	position: relative;
	background-color: $primary;
	text-align: center;
	padding: 5px 0;


	.flush {
		padding-left: 0;
		padding-right: 20px;

		@media(min-width: $nav-breakpoint) {
			padding-left: 0;
			padding-right: 16px;
		}
	}

	p {
		color: #fff;
	}

	a {
		color: #fff;
		text-decoration: underline;
		transition: color 0.2s;

		&:hover,
		&:active,
		&:focus {
			color: rgba(#fff, 0.6);
		}
	}

	button {
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);

		@media(min-width: $nav-breakpoint) {
			right: 50px;
		}

		svg {
			width: 12px;
			height: auto;
		}
	}
}

.site-notice-bottom {
	position: fixed;
	bottom: 8px;
	left: 8px;
	right: 8px;
	border-radius: 4px;

	@media(min-width: $nav-breakpoint) {
		left: auto;
		width: 50%;
		text-align: left;
		bottom: 16px;
		right: 16px;
		padding-top: 8px;
		padding-bottom: 8px;

		button {
			right: 12px;
		}
	}
}