#wpadminbar {
	position: fixed;
}

body.admin-bar {
	.site-header {
		top: 46px;

		@media screen and (min-width: 782px) {
			top: 32px;
		}
	}

	.main-navigation {
		@media screen and (max-width: $nav-breakpoint-max) and (max-width: 782px) {
			top: 115px;
		}

		@media screen and (min-width: 782px) and (max-width: $nav-breakpoint-max) {
			top: 101px;
		}
	}
}
