.validation_error {
	color: $error;
	font-weight: 700;
	margin-top: 15px;
	font-family: $header-font;

	@include media-breakpoint-up(lg) {
		margin-top: 20px;
	}
}

.validation_message {
	color: $error;
	font-weight: 700;
	margin-top: 5px;
	font-family: $header-font;
}

.gfield_error {
	/*
	 * Text, email, url, tel, number fields, textarea, select
	 */
	input[type="text"],
	input[type="email"],
	input[type="url"],
	input[type="tel"],
	input[type="number"],
	textarea,
	select {
	    border: 1px solid $error;
	}

	/*
	 * Multi File Upload
	 */

	.gform_fileupload_multifile .gform_drop_area {
		border: 1px dashed $error;
	}
}