/*
 * Woocommerce Layouts 
 */

@import 'account';
@import 'cart';
@import 'category';
@import 'checkout';
@import 'login';
@import 'order-confirmation';
@import 'product-listing';
@import 'product';
@import 'notices';

.button.woocommerce_waitlist {
	float: none !important;
	margin-bottom: 30px !important;
}

.wcwl_intro p {
	margin-bottom: 0;
}

.woocommerce #respond input#submit.alt,
.woocommerce a.button.alt,
.woocommerce button.button.alt,
.woocommerce input.button.alt,
.button.woocommerce_waitlist {
	background-color: $primary;
	transition: background 0.25s;
	border-radius: 39px;
	padding: 20px 30px;

	&:hover,
	&:active,
	&:focus {
		background-color: $primary-dark;
	}
}

.woocommerce{
	.col-1, .col-2{
		max-width:none;
		flex: none;
		padding: 0;
	}
}

body .woocommerce-store-notice, body p.demo_store {
	background-color: $primary;
	z-index: +2;
	position: relative;
	padding: 10px;
	box-shadow: none;
	top: 0 !important;
}

body.customize-partial-edit-shortcuts-shown .woocommerce-store-notice,
body.customize-partial-edit-shortcuts-shown p.demo_store {
	display: block !important;
}

.woocommerce-store-notice__dismiss-link {
	transition: color 0.25s;
	
	&:hover,
	&:active,
	&:focus {
		color: $white-60;
	}
}

.woocommerce .woocommerce-breadcrumb {
	padding: 40px 0 30px;
	margin: 0;

	@include media-breakpoint-up(lg) {
		padding-top: 60px;
	}

}

.wcpf-field-color-list .wcpf-heading-label, .wcpf-field-text-list .wcpf-heading-label, .wcpf-field-box-list .wcpf-heading-label, .wcpf-field-checkbox-list .wcpf-heading-label, .wcpf-field-radio-list .wcpf-heading-label, .wcpf-field-drop-down-list .wcpf-heading-label, .wcpf-field-price-slider .wcpf-heading-label {
	font-family: $header-font;
	letter-spacing: 1px;
}

.wcpf-field-checkbox-list .wcpf-checkbox-list .wcpf-checkbox-item .wcpf-checkbox-label {
	color: $black;
	font-size: $paragraph-font-size;
	font-family: $header-font;
}

.wcpf-field-checkbox-list .wcpf-checkbox-list .wcpf-checkbox-item.checked > .wcpf-checkbox-item-inner > .wcpf-checkbox-label .wcpf-input-container::after {
	border-color: $primary;
}

.wcpf-field-button .wcpf-button {
	font-family: $header-font;
	color: $black;
	text-transform: uppercase;
	font-size: 14px;
	letter-spacing: 1px;
	padding: 10px 20px;
}

.single-product {

	.pswp__bg {
		background: $black !important;
		opacity: 0.8 !important;
	}

	.pswp__caption {
		display: none;
	}
}

.select2-container .select2-selection--single {
	height: 41px;
}

.select2-container--default .select2-selection--single {
	border-color: $black-25;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
	line-height: 41px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
	height: 40px;
	right: 5px;
}

.woocommerce form .form-row label {
	font-family: $header-font;
}

.shop_table {

	tbody {

		tr {

			td:first-child {
				font-family: $header-font;
			}
		}
	}
}

.cart_totals th {
	font-family: $header-font;
}

.wc_payment_method label {
	font-family: $header-font;
	color: $black;
}

.star-rating {
	color: gold;
}

.wcsatt-options,
ul.wcsatt-options-product,
.wcsatt-options-product-dropdown {
	padding: 0 !important;
}

.woocommerce div.product p.price ins, .woocommerce div.product span.price ins {
	font-weight: 400;
}

.woocommerce div.product p.price, .woocommerce div.product span.price {
	font-size: 1em;
}

.woocommerce span.onsale {
	width: 50px;
	height: 50px;
}

.woocommerce #respond input#submit.alt.disabled, .woocommerce #respond input#submit.alt.disabled:hover, .woocommerce #respond input#submit.alt:disabled, .woocommerce #respond input#submit.alt:disabled:hover, .woocommerce #respond input#submit.alt:disabled[disabled], .woocommerce #respond input#submit.alt:disabled[disabled]:hover, .woocommerce a.button.alt.disabled, .woocommerce a.button.alt.disabled:hover, .woocommerce a.button.alt:disabled, .woocommerce a.button.alt:disabled:hover, .woocommerce a.button.alt:disabled[disabled], .woocommerce a.button.alt:disabled[disabled]:hover, .woocommerce button.button.alt.disabled, .woocommerce button.button.alt.disabled:hover, .woocommerce button.button.alt:disabled, .woocommerce button.button.alt:disabled:hover, .woocommerce button.button.alt:disabled[disabled], .woocommerce button.button.alt:disabled[disabled]:hover, .woocommerce input.button.alt.disabled, .woocommerce input.button.alt.disabled:hover, .woocommerce input.button.alt:disabled, .woocommerce input.button.alt:disabled:hover, .woocommerce input.button.alt:disabled[disabled], .woocommerce input.button.alt:disabled[disabled]:hover {
	background-color: $primary;
}

.wc_gc_field {

	label {
		display: block;
		margin-bottom: 5px;
	}
}

#wc_gc_cart_redeem_send {
	@extend .btn-primary;
	min-width: 0;
	padding: 14px;
	min-width: 100px;
	margin-left: 20px;
}

#wc_gc_cart_redeem_form {
	display: flex;
	align-items: center;
}

#wc_gc_cart_code {
	width: 100% !important;
}

.place-order {

	.add_gift_card_form {
		max-width: 400px;
	}
}

.place-order .add_gift_card_form #wc_gc_cart_redeem_form input[type="text"], .place-order .add_gift_card_form #wc_gc_cart_redeem_form button {
	width: auto;
}

.place-order .add_gift_card_form #wc_gc_cart_redeem_form input[type="text"] {
	margin-bottom: 0;
}

.product .nyp .nyp-input, .wcopc .product-item .nyp .nyp-input {
	text-align: left !important;
	font-weight: 400 !important;
}

.product .nyp label, .wcopc .product-item .nyp label {
	font-weight: 400;
	margin-bottom: 5px;
}

.product .single_variation_wrap .nyp {
	padding-bottom: 0.8em;
}

.wc-block-components-price-slider__range-input-progress {
	background: $black;
}

.wc-block-checkbox-list {

	label {
		margin-left: 8px;
	}
}

.wc-block-product-categories-list {
	margin-left: 0;
	padding-left: 0;
	list-style-type: none;
	max-height: 400px;
	overflow: scroll;

	li {

		a {
			color: $black;

			&:active,
			&:hover,
			&:focus {
				color: $primary;
			}
		}
	}

	ul {
		padding-left: 16px;
		margin-bottom: 0;
	}
}

.widget_heading {
	margin-bottom: -24px;
}