.ginput_container_address {
    @include make-row;

    span {
        @include make-col-ready;

        margin-top: 15px;

        label {
            color: $black-60;
            display: block;
            font-family: $header-font;
            font-size: 13px;
        }
    }

    .address_city,
    .address_state,
    .address_zip,
    .address_country {
        @include media-breakpoint-up(lg) {
            @include make-col(6);
        }
    }

    .address_country {
        position: relative;

        &::after {
            background-image: url('../img/forms/select-chevron.svg');
            background-repeat: no-repeat;
            content: '';
            display: block;
            height: 7px;
            pointer-events: none;
            position: absolute;
            width: 12px;
            right: 35px;
            top: 50%;
            transform: translateY(-50%) translateY(12px);
        }
    }
}

.field_sublabel_above {
    .ginput_container_address {
        span label {
            margin-bottom: 5px;
        }
    }
}

.field_sublabel_below {
    .ginput_container_address {
        span label {
            margin-top: 5px;
        }
    }
}