.template-coming-soon {
	min-height: 100vh;
}

	.coming-soon {
		position: relative;
		min-height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
	}

		.coming-soon-content {
			position: relative;
			z-index: +100;
			width: calc(100% - 48px);
			max-width: 600px;
			margin-left: auto;
			margin-right: auto;
		}

		.coming-soon-bg-image {
			position: absolute;
			z-index: +50;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}