.blog .site-main {
	background-color: $black-5;
}

.post-list {}

	.post-list-title {
		padding: 30px 0;
		margin-bottom: 0;
	}

	.post-list-featured {
		padding-top: 30px;

		@include media-breakpoint-up(lg) {
			padding-top: 60px;
		}
	}

	.post-list-header {
		padding: 30px 0;

		@include media-breakpoint-up(md) {
			display: flex;
			justify-content: flex-end;
		}
	}

		.post-list-filter {
			position: relative;
			width: 100%;
			min-width: 235px;

			@include media-breakpoint-up(md) {
				width: auto;
			}

			&.open {
				.post-list-filter-dropdown {
					opacity: 1;
					pointer-events: auto;
				}
			}
		}

			.post-list-filter-toggle {
				appearance: none;
				display: flex;
				flex-wrap: nowrap;
				justify-content: space-between;
				align-items: center;
				width: 100%;
				padding: 15px 20px;
				background-color: transparent;
				border: 2px solid $black-5;

				svg {
					display: block;
					margin-left: 10px;
				}

			}

			.post-list-filter-dropdown {
				position: absolute;
				z-index: +10;
				top: 100%;
				left: 0;
				right: 0;
				opacity: 0;
				margin: 0;
				padding: 10px 0;
				list-style: none;
				pointer-events: none;
				background-color: #fff;
				border: 2px solid $black-5;
				border-top: none;
				transition: opacity 0.25s;

				li {

					a {
						display: block;
						padding: 10px 20px;
						text-decoration: none;

						&:hover,
						&:active,
						&:focus {
							background-color: $black-5;
						}
					}
				}
			}

	.post-list-content-wrap {

		@include media-breakpoint-up(lg) {
			display: flex;
			flex-wrap: nowrap;
		}
	}


		.post-list-content {
			padding-bottom: 40px;
			width: 100%;

			@include media-breakpoint-up(md) {
				padding-bottom: 20px;
			}
			
			.post-card {
				margin-bottom: 20px;

				@include media-breakpoint-up(md) {
					margin-bottom: 40px;
				}
			}
		}

		.post-list-sidebar {

			@include media-breakpoint-up(lg) {
				width: 30%;
				min-width: 30%;
				margin-left: 40px;
			}
		}

	.post-list-footer {
		text-align: center;

		.pagination {
			padding-bottom: 30px;

			@include media-breakpoint-up(lg) {
				padding-bottom: 60px;
			}
		}
	}