$x-positions: 0 25 50 75 100;
$y-positions: 0 25 50 75 100;

@each $x-position in $x-positions {
	@each $y-position in $y-positions {
		.position-#{$x-position}-#{$y-position} {
			object-fit: cover;
			object-position: $x-position * 1% $y-position * 1%;
		}

		picture.position-#{$x-position}-#{$y-position} {
			
			img {
				object-fit: cover;
				object-position: $x-position * 1% $y-position * 1%;
			}
		}
	}
}
