.linktree-content {
    padding: 60px 0;
    max-width: 680px;
    margin-left: auto;
    margin-right: auto;
    width: calc(100% - 50px);
    text-align: center;

    h1 {
        font-size: 20px;
        line-height: 1;
        margin-bottom: 24px;
        color: #fff;
    }
}

    .linktree-logo {
        display: inline-block;
        width: 100px;
        height: 100px;
        object-fit: cover;
        border-radius: 100%;
        margin-bottom: 24px;
    }

    .linktree-link {
        display: block;
        margin-bottom: 16px;
        padding: 16px 24px;
        color: #fff;
        border: 1px solid #fff;
        border-radius: 30px;
        transition: color 0.25s, background-color 0.25s;
        font-size: 16px;

        &:active,
        &:hover,
        &:focus {
            background-color: #fff;
            color: $black;
        }
    }