.author-header {
    padding: 160px 0 72px;
    background-color: $black;
  
    h1,h2,h3,h4,h5,h6,p,ol,ul {
      color: #fff;
    }
  
    h1 {
      font-size: 48px;
      line-height: 56px;
      margin-bottom: 8px;
    }
  }
  
  .author-description {
    padding: 48px 0 0;
  
    @include media-breakpoint-up(lg) {
      padding: 72px 0 0;
    }
  
    .flex {
  
      @include media-breakpoint-up(lg) {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }
  
      > div {
        max-width: 800px;
  
        @include media-breakpoint-up(lg) {
          margin-left: 32px;
        }
      }
    }
  }
  
  .author-sub-header {
    margin: 8px 0 16px;
  
    @include media-breakpoint-up(md) {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  
    .author-job-title {
      display: inline-block;
      background-color: $white-10;
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
      padding: 8px 12px;
      border-radius: 16px;
      text-transform: uppercase;
      color: #fff;
    }
  
    .author-link {
      display: inline-flex;
      align-items: center;
      font-size: 12px;
      line-height: 14px;
      padding: 8px 12px;
      border-radius: 16px;
      text-transform: uppercase;
      color: #fff;
      background-color: $accent-1;
      transition: background-color 0.25s;
  
      &:active,
      &:hover,
      &:focus {
        background-color: $accent-1-dark;
      }
  
      svg, span {
        display: block;
      }
  
      svg {
        width: 16px;
        height: auto;
        margin-right: 12px;
      }
    }
  }
  
  .author-quote {
    background-color: $white-10;
    border-radius: 16px;
    padding: 40px;
  
    @include media-breakpoint-up(lg) {
      display: flex;
      padding: 80px;
      border-radius: 24px;
    }
  }
  
    .author-quote-image {
  
      @include media-breakpoint-up(lg) {
        min-width: 214px;
        margin-right: 40px;
      }
  
      img {
        display: block;
        width: 214px;
        height: 214px;
        border-radius: 100%;
        object-fit: cover;
      }
    }
  
    .author-quote-text-content {
      margin-bottom: 16px;
  
      @include media-breakpoint-up(lg) {
        margin-bottom: 24px;
      }
  
      p {
        font-weight: 700;
        font-size: 32px;
        line-height: 38px;
        letter-spacing: 0.25px;
      }
    }
  
    .author-quote-title {
  
      p {
        margin-bottom: 0;
      }
    }
  
  .archive.author .post-list {
    padding-top: 48px;
  
    @include media-breakpoint-up(lg) {
      padding-top: 72px;
    }
  }
  
  .post-list-content-header {
    margin-bottom: 16px;
  
    h4 {
      margin-bottom: 0;
    }
  }