.post-card {
	display: block;
	text-decoration: none;
	background-color: #fff;

	&:hover,
	&:active,
	&:focus {
		
		.post-card-title {
			color: $primary;
		}
	}
}

	.post-card-image {}

		.post-card-image-wrap {
			position: relative;
			padding-bottom: 56.25%;

			img {
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}

	.post-card-content {
		padding: 30px 20px;
		text-align: center;
	}

		.post-card-meta {
			font-family: $header-font;
			color: $black-60;
			margin-bottom: 0;
			font-size: 13px;
		}

		.post-card-title {
			font-size: 30px;
			margin-bottom: 20px;
			transition: color 0.25s;
		}

.post-layout-list {

	.post-card {
		background-color: transparent;

		@include media-breakpoint-up(md) {
			display: flex;
			align-items: flex-start;
		}
	}

		.post-card-image {
			
			@include media-breakpoint-up(md) {
				width: 40%;
			}
		}

		.post-card-content {
			text-align: left;
			
			@include media-breakpoint-up(md) {
				padding: 0;
				padding-left: 30px;
				width: 60%;
			}
		}
}