.cover {
	position: relative;
	
	&::after {
		content: '';
		background-color: rgba($black, 0.6);
		bottom: 0;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		z-index: +10;
		transition: background-color 0.25s;
	}
}

	.cover-content {
		position: relative;
		z-index: +15;
	}

	.cover-image {
		position: absolute;
		z-index: +5;
		height: 100% !important;
		width: 100%;
		bottom: 0;
		left: 0;
		right: 0;
		top: 0;
	}

	picture.cover-image {

		img {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			// object-fit: cover;
		}
	}

	.cover-video {
		position: absolute;
		z-index: +6;
		height: 100% !important;
		width: 100%;
		bottom: 0;
		left: 0;
		right: 0;
		top: 0;
		object-fit: cover;
	}