.module-featured-3-col {

	.module-part-content {
		text-align: center;
		max-width: 720px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 20px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}

	.column {
		display: flex;
		flex-direction: column;
	}

		.column-content {
			width: 100%;
			padding: 30px 0 0;
			text-align: center;
			flex-grow: 1;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}

			.column-image {
				position: relative;
				padding-bottom: 56.25%;
				
				img {
					position: absolute;
					z-index: +5;
					top: 0;
					left: 0;
					display: block;
					width: 100%;
					height: 100%;
				}

				.btn-play {
					position: absolute;
					z-index: +10;
					top: 50%;
					transform: translateY(-50%) scale(0.8);
					left: 0;
					right: 0;
					margin-left: auto;
					margin-right: auto;
				}
			}

			.column-title {
				font-size: 26px;
				font-weight: 700;
				margin-bottom: 20px;
			}

			.column-description {}

			.column-content-footer {
				margin-top: 24px;
			}

	.flex-col-3 {
		@include media-breakpoint-up(lg) {
			margin-bottom: -40px;
		}
	}

	.flex-col-2 {

		@include media-breakpoint-up(lg) {
			margin-bottom: -40px;
		}
	}

	.flex-col-4 {

		@include media-breakpoint-up(xl) {
			margin-bottom: -40px;
		}
	}

	&.module-setting-image-display-contain {

		.column-image img {
			object-fit: contain;
		}
	}
}
