.module-testimonial-slider {

	.module-part-heading {
		text-align: center;
		margin-bottom: 30px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}

	.module-part-content {
		padding-left: 16px;
		padding-right: 16px;

		@include media-breakpoint-up(sm) {
			padding-left: 0;
			padding-right: 0;
		}
	}

		.module-part-title {

			@include media-breakpoint-up(lg) {
				margin-bottom: 0;
			}
		}

		.testimonial-slide {
			height: auto;
			padding: 32px 16px;
			background-color: #ffffff;
			border-radius: 5px;

			@include media-breakpoint-up(lg) {
				padding: 48px;
			}
		}

		.slide-content {

			blockquote p {

				@include media-breakpoint-up(lg) {
					font-size: 18px;
					// line-height: 47px;
				}
			}
		}

	.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
		position: relative;
		bottom: 0;
		margin-top: 30px;

		.swiper-pagination-bullet {
			width: 12px;
			height: 12px;
			background-color: rgba($black, 0.2);
			transition: background-color 0.25s;
			opacity: 1;
			margin: 0 7px;
		}

		.swiper-pagination-bullet:not(.swiper-pagination-bullet-active) {
			&:active,
			&:hover,
			&:focus {
				background-color: rgba($black, 0.6);
			}
		}

		.swiper-pagination-bullet-active {
			background-color: $primary;
		}
	}

	&.module-setting-bg-white {

		.testimonial-slide {
			background-color: $black-5;
		}
	}
}

.testimonial {
	width: 100%;
	text-align: center;

	blockquote {
		margin: 0;
		padding: 0;

		p {
			color: #201d39;
			font-size: 18px;
			font-weight: 400;
			// line-height: 34px;
			margin-bottom: 32px;

			@include media-breakpoint-up(lg) {
				margin-bottom: 48px;
			}
		}
	}

	footer {

	}

	img {
		display: inline-block !important;
		width: 66px;
		height: 66px;
		margin-bottom: 20px;
		border-radius: 100%;
	}

	cite {
		display: block;
		font-style: normal;
		color: #201d39;
		font-size: 18px;
		font-weight: 500;
		margin-bottom: 0;

		span {
			display: block;
			color: #7b798b;
			font-size: 15px;
			font-weight: 400;
			line-height: 1.3;
			margin-top: 5px;
		}
	}
}