/*
 * Text, email, url, tel, number fields
 */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="tel"],
input[type="number"],
input[type="password"],
input[type="search"],
textarea,
.gmw-form-wrapper input[type=text],
.gmw-form-wrapper input[type=number],
.gmw-form-wrapper select,
.gmw-form-wrapper textarea {
    border: 1px solid $black-25;
    border-radius: 4px;
    background-color: #fff;
    height: 41px;
    padding: 0 10px;
    width: 100%;
    font-family: $paragraph-font;
    font-size: 16px;
    color: $black;
}

/*
 * Textarea
 */
textarea,
.gmw-form-wrapper textarea {
    height: auto;
    padding: 10px;
}
